.paginator * {
    padding: 0;
    margin: 0;
}
.paginator button {
    width: 35px;
    height: 35px;
    font-weight: bold;
    border: none;
    border-radius: 17px;
    margin: 2px;
    background-color: #f6f6f6;
    box-shadow: 1px 1px 3px 3px lightgrey;

}
.paginator button:hover {
    cursor: pointer;
}

.currentPage {
    color: red;
    font-size: 1.2em;
}
.start img, .end img, .previous img, .next img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}
.end img, .next img {
    transform: rotate(180deg);
}
start button {
    background: none;
    border-radius: 17px;
}